.app {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: flex-start;
  color: white;
}

.header {
  font-size: 44px;
  font-style: normal;
  font-weight: normal;
  text-align: center;

  margin-top: 8vmin;
  @media screen and (min-width: 600px) {
    margin-top: 30vh;
    font-size: 64px;
  }
}

.view-work-btn {
  padding: 2vmin 4vmin;
  border: 2px solid white;
  border-radius: 4px;
  font-size: 24px;
  margin-top: 32px;

  &:hover {
    cursor: pointer;
    background-color: #ffffff11;
  }

  @media screen and (min-width: 600px) {
    font-size: 32px;
    margin-top: 64px;
  }
}

.inline-span {
  display: inline;
  color: #F4425A;
}

.work {
  margin-top: 10vh;
  width: 100%;
}

.timeline-end {
  width: 4px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255,1)), to(rgba(255, 255, 255,0)), color-stop(.5,#fff));
}

.timeline-start {
  width: 4px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255,1)), to(rgba(255, 255, 255,0)), color-stop(.5,#fff));
}

.interested {
  margin-top: 64px;
  margin-bottom: 128px;
  font-size: 36px;
  font-style: normal;
  font-weight: normal;
  text-align: center;
}

.link {
  color: #F4425A;
  text-decoration: none;

  &:hover {
    color:#a33d4b
  }
}

.footer {
  @media screen and (min-width: 600px) {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: left;
    margin-left: 32px;
  }

  margin-bottom: 16px;
  text-align: center;
  display: inline-block;

  & .footer-icon {
    margin-right: 16px;

    & svg {
      width: 48px;
      height: 48px;
      
      color: white;

      &:hover {
        color: #ccc;
        cursor: pointer;
      }
    }
  }

  & .icons{
    @media screen and (min-width: 600px) {
      display: inline-block;
    }
  }

  & .get-in-touch {
    @media screen and (min-width: 600px) {
      display: inline-block;
      position: absolute;
      right: 32px;
    }

    vertical-align: middle;
    font-size: 32px;
    text-align: center;
    line-height: 64px;

    & .envelope-link {
      display: inline-block;
      color: white;
      background-color: #F4425A;
      border-radius: 50%;
      width: 60px;
      height: 60px;
      margin-left: 16px;

      &:hover {
        background-color: #a33d4b;
      }

      & svg {
        width: 32px;
        height: 32px;
        margin-top: -10px;
        vertical-align: middle;
      }
    }
  }
}